import { Carousel } from "react-bootstrap";
import ExampleCarouselImage from "./ExampleCarouselImage";

function ProjectItem(props) {
    const { imageNumber } = props;

    // Define style for the carousel caption text
    const captionStyle = {
        fontSize: '18px',
        fontWeight: 'bold',
    };

    // Style for the description
    const descriptionStyle = {
        fontSize: '22px',
        fontWeight: 'normal',
        marginBottom: '20px',
        paddingTop: '50px',
        lineHeight: '1.6',
    };

    // Style for the image container
    const imageContainerStyle = {
        position: 'relative',
        width: '100%',
        height: 'auto',
        maxWidth: '100%',
        textAlign: 'center', // Center images in the container
    };

    // Style for the image itself
    const imageStyle = {
        maxWidth: '100%', // Ensures image width fits the container
        maxHeight: '500px', // Limits the image height for consistent display
        objectFit: 'contain', // Ensures the whole image is visible within its container
    };

    return (
        <div className="pb-5">
            <h1 className="pt-4">{props.title}</h1>
            <p>{props.description}</p>
            <div className="container mt-4">
                <div className="row">
                    <Carousel>
                        {[...Array(imageNumber).keys()].map((index) => (
                            <Carousel.Item key={index}>
                                {/* Description */}
                                <p style={descriptionStyle}>{props[`desc${index + 1}`]}</p>

                                {/* Image */}
                                <div className="carousel-image-container" style={imageContainerStyle}>
                                    <ExampleCarouselImage
                                        imgSrc={props[`image${index + 1}`]}
                                        style={imageStyle}
                                    />
                                    <div>
                                        {/* Caption */}
                                        <div className="carousel-caption" style={captionStyle}>
                                            <h3>{props[`lbl${index + 1}`]}</h3>
                                        </div>
                                        {/* Bottom buttons */}
                                        <div className="carousel-bottom-buttons">
                                            {/* Add your bottom buttons here */}
                                        </div>
                                    </div>
                                </div>
                            </Carousel.Item>
                        ))}
                    </Carousel>
                </div>
            </div>
        </div>
    );
}

export default ProjectItem;
